<template>
  <div class="inputcontainer" @click="click()" :class="{'error':!status}">
    <label :class="info">{{label}}</label>
    <div  class="content" readonly="readonly">{{value}}</div>
    <div class="tanhao" v-if="!status">
      <el-tooltip  class="item" effect="dark" content="点亮表达有误" placement="top">
        <i class="iconfont icon-tishi"></i>
      </el-tooltip>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'vertifyInputcomponent',
    props:{
      info:{
        default:''
      },
      changeinputstatus:{
        type:Function
      },
      status:{

      },
      label:{
        type:String
      },
      value:{
        ddefault:''
      }
    },
    data () {
      return {
      }
    },
    methods:{
      input(value){
        this.$emit('input', value)
      },
      click(v){
        this.changeinputstatus()
      }
    }
  }
</script>

<style lang="scss" scoped>
  .inputcontainer{
    &:first-child{
     padding-top: 0;
     }
    display: flex;
    //justify-content: space-between;
    width:100%;
    padding: 0.15rem 0 0.15rem 0;
    label{
      //border:1px solid red;
      height:.28rem;
      display:flex;
      align-items: center;
      font-size: .14rem;
      width: 1.3rem;
      //border:1px solid red;
      &.careerInfo{
        width:1.8rem;
      }

    }
    div.content{
      max-width:2rem;
      //border:1px solid red;
      margin-right: .3rem;
      //flex-grow: 1;
      display:flex;
      align-items: center;
      font-size: .16rem;
      color: #263238;
    }
    div.tanhao{
      margin:0;
      padding:0;
      flex-grow: 1;
      //border:1px solid red;
      display:flex;
      align-items: center;
      justify-content: flex-end;
      i{
        color:#FC7563;
        margin:0;
        padding:0;
        font-size: .23rem;
      }
    }
  }
  .onlyVer{
    .tabbody .inputcontainer div.content{
      // max-width: 5rem;
    }
  }
</style>



