<template>
  <div>
    <ul class="tabheader">
      <li  style="cursor:pointer" :class="{'active':activekey==key1}" v-for="(value1,key1) in title" :key="key1" @click="click(key1)">{{value1}}</li>
    </ul>
    <ul class="tabbody">
      <slot name="tabitem" v-for="(value2,key2) in title" :tabitem="key2" :activekey="activekey" ></slot>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'tabcoponent',
  props: {
    defaultkey: {
      type: String
    },
    title: {
      type: Object
    },
    change: {
      type: Function
    }
  },
  data () {
    return {
      activekey: this.defaultkey
    }
  },
  methods: {
    click (key) {
      this.change(key, this.activekey)
      this.activekey = key
    }
  }
}
</script>

<style lang="scss" scoped>
  $blue:#08D0EE;
  ul,li{
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .tabheader{
    //border:1px solid red;
    height:0.58rem;
    align-items:center;
    display:flex;
    background:white;
    border-bottom: 1px solid #E9EDEF;
    li{
      margin:0 0.6rem;
      font-size: .2rem;
      height: .58rem;
      line-height: .58rem;
      font-size: .16rem;
      color: #8597A1;
      &.active{
        color:#263238;
        border-bottom:.03rem solid #20C6BA;
      }
    }
  }
  .tabbody{
    margin:0.03rem 0;
    li{
      padding:0.58rem 0rem;
      background: white;
    }
  }
</style>



