<template>
   <div class='updatecoach' @click='hideSelect'>
       <div class='top'>
           <span class='fundInfo' @click='goback()'>{{titleText}}</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>教练信息</span>
       </div>
       <div class='coachInfo'>
          <div :class="{verify: true, 'onlyVer': !type ? false : true}">
              <Tab :title="config.title" :defaultkey="defaultkey" :change="changeTab" ref="tab">
                <li slot="tabitem" slot-scope="props" v-show="props.activekey==props.tabitem" class="slot">
                  <div class="loadingcontainer" v-loading="infoloading">
                    <div class="left">
                      <template  v-for="(item1,index1) in currentinfo">
                        <vertify-input :info="props.activekey" :label="config[props.activekey][key2]" :value="value2|filterinput(key2)" :changeinputstatus="()=>changeInputStatus(index1)"  :status="item1['status']" v-for="(value2,key2) in item1"   v-if="config[props.activekey][key2]&&typeof value2!=='object'" :key="key2"></vertify-input>
                      </template>
                      <div v-if="certifate&&currentinfo" class='certifate'>
                        <label>资质证书:</label>
                        <div class='right'>
                          <div v-for="(item, index) in certList" :key="index" class="inputcontainer" @click="changeCertStatus(item)" :class="{'error':!item.status}">
                              {{item.name}}
                          <div class="tanhao" v-if="!item.status">
                            <el-tooltip  class="item" effect="dark" content="点亮表达有误" placement="top">
                              <i class="iconfont icon-tanhao"></i>
                            </el-tooltip>
                          </div>
                        </div>
                        </div>
                      </div>
                    </div>
                    <div class="right">
                      <template v-for="(item1,index1) in currentinfo">
                        <vertify-photo  :class="defaultkey" :label="config[props.activekey][key2]" :photos="value2" :visPhoto="visPhoto" :changephotostatus="(index2)=>changePhotoStatus(index1,index2,key2)" v-for="(value2,key2) in item1" v-if="config[props.activekey][key2]&&typeof value2=='object'" :key="key2"></vertify-photo>
                      </template>
                    </div>
                  </div>
                </li>
              </Tab>
              <div class="footer" v-show="currentinfo && (infostatus=='Completed') && status === 'WaitingCertificated' || showVer" v-if="status === 'WaitingCertificated'">
                <el-button type="primary" :loading="iconloading&&!culculatestatus" plain round class="unpass" :disabled="culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-cuo"></i>信息有误</el-button>
                <el-button type="primary" :loading="iconloading&&culculatestatus" plain round class="pass" :disabled="!culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-kh_1"></i>审核通过</el-button>
              </div>
              <div class="footer" v-show="currentinfo && !verify && (vertifyList.indexOf(activekey) < 0) && (show || (infostatus=='NotPassed')) || (showVer)" v-if="status === 'CertificateFailed'">
                <el-button type="primary" :loading="iconloading&&!culculatestatus" plain round class="unpass" :disabled="culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-cuo"></i>信息有误</el-button>
                <el-button type="primary" :loading="iconloading&&culculatestatus" plain round class="pass" :disabled="!culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-kh_1"></i>审核通过</el-button>
              </div>
              <div class="footer" v-show="currentinfo && (vertifyList.indexOf(activekey) < 0) && show && !verify || showVer" v-if="status === 'CertificateSuccessed'">
                <el-button type="primary" :loading="iconloading&&!culculatestatus" plain round class="unpass" :disabled="culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-cuo"></i>信息有误</el-button>
                <el-button type="primary" :loading="iconloading&&culculatestatus" plain round class="pass" :disabled="!culculatestatus" @click="vertify"><i v-if="!iconloading" class="iconfont icon-kh_1"></i>审核通过</el-button>
              </div>
          </div>
          <div class='editInfo' id='editInfo' v-if="type !== 'verify'">{{type}}
            <el-form ref="form" :model="form" label-width="80px">
              <!-- <div class='secInfo'>
                    <span class='sec_left'>所属分店：</span>
                    <span class='sec_right'>{{form.branchOffice}}</span>
                </div> -->
                <el-form-item label="所属分店：" class='formNew newFlex'>
                  <el-input v-model="form.branchOffice" class='inputNew' :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="入职时间：" class='formNew hireDate newFlex'>
                    <!-- <el-input v-model="form.name" class='inputNew' placeholder="请输入店名"></el-input> -->
                    <el-date-picker
                      v-model="form.hireDate"
                      type="date"
                      placeholder="选择日期">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="教练介绍：" class='TextArea formNew newFlex'>
                  <el-input v-model="form.coachBrief" class='inputNew'  type="textarea" placeholder="请输入介绍" :maxlength="300"></el-input>
                </el-form-item>
                <div class='checkForm'>
                  <div class='leftCheck'>
                    <div class='fir'>私教课种类：</div>
                    <div class='sec'>(可多选)</div>
                  </div>
                  <el-form-item label="" class='checkobox special'>
                      <!-- <el-radio-group v-model="form.courseLevelId">
                        <el-radio v-for="item in privateList" :label="item.id" :key="item.id">{{item.name}}</el-radio>
                      </el-radio-group> -->
                      <el-checkbox :indeterminate="isIndeterminate2" v-model="checkAll2" @change="handleCheckAllChange2">全选</el-checkbox>
                      <el-checkbox-group v-model="form.privateCourseIds"  @change="handleCheckedPrivate">
                        <el-checkbox v-for="item in privateList" :label="item.id" :key="item.id">{{item.name}}</el-checkbox>
                      </el-checkbox-group>
                  </el-form-item>
                </div>
                <div class='checkForm'>
                  <div class='leftCheck'>
                    <div class='fir'>特色课种类：</div>
                    <div class='sec'>(可多选)</div>
                  </div>
                  <el-form-item label="" class='checkobox special'>
                    <el-checkbox :indeterminate="isIndeterminate1" v-model="checkAll1" @change="handleCheckAllChange1">全选</el-checkbox>
                    <el-checkbox-group v-model="form.specialCourseIds"  @change="handleCheckedCities">
                      <el-checkbox v-for="item in specialList" :label="item.id" :key="item.id">{{item.name}}</el-checkbox>
                    </el-checkbox-group>
                  </el-form-item>
                </div>
                <div class='checkForm'>
                  <div class='leftCheck'>
                    <div class='fir'>团课种类：</div>
                    <div class='sec'>(可多选)</div>
                  </div>
                  <el-form-item label="" class='checkobox special'>
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleCheckAllChange">全选</el-checkbox>
                    <el-checkbox-group v-model="form.groupCourseIds" @change="handleCheckedCitiesChange">
                      <el-checkbox v-for="item in groupList" :label="item.id" :key="item.id">{{item.name}}</el-checkbox>
                    </el-checkbox-group>
                 </el-form-item>
                </div>
                <div class='btn'>
                    <div class='submit cancel' @click="goback()">取消</div>
                    <div class='submit' @click="submit('form')">提交</div>
                </div>
            </el-form>
          </div>
       </div>
   </div>
</template>
<script>
import {getSessionStorage, isApiResponseOk} from '../../utils/com.js'
import {updatecoach, getallprivate, getCoachInfo, validatecoachinfo, getgroupsimple, getgroupcourse, queryspecial} from '../../services/service.js'
import Tab from '../../components/Tab'
import VertifyInput from '../../components/VertifyInput'
import VertifyPhoto from '../../components/VertifyPhoto'
import {timeformer} from '../../utils/com.js'
import Vue from 'vue'
export default {
  data () {
    return {
      form: {
        value1: '',
        value5: '请选择',
        personId: '',
        personImage: '',
        personName: '',
        personSex: '',
        personPhone: '',
        personBirthday: '',
        personalIdNo: '',
        personalType: '',
        branchOffice: '',
        hireDate: '',
        specialCourseIds: [],
        groupCourseIds: [],
        privateCourseIds: []
      },
      options: [
        {label: '搏击私教课', value: '1', checked: false},
        {label: '精英私教课', value: '2', checked: false},
        {label: '主管私教课', value: '3', checked: false},
        {label: '健身冠军私教课', value: '4', checked: false},
        {label: '普通常规私教课', value: '5', checked: false}
      ],
      isShow: false,
      privateList: [],
      groupList: [],
      specialList: [],
      sortnames:{
          basicInfo:['name','sex','phoneNum','cardNum','birthday','cards'],
          degreeInfo:['highestDegree','graduatedSchool','profession','graduatedDate','graduatedCertifications'],
          careerInfo:['workDate','gymnasiumName','gymnasiumAddress','workProve','careerCertificationNames', 'careerCertification']
        },
        config: {
          title:{
            basicInfo:'基本信息',
            degreeInfo:'学历信息',
            careerInfo:'职业资质'
          },
          basicInfo:{
            name:'名字:',
            sex:'性别:',
            phoneNum:'手机:',
            cardNum:'身份证号:',
            birthday:'出生日期:',
            cards:'身份证照片:'
          },
          degreeInfo:{
            highestDegree:'最高学历:',
            graduatedSchool:'毕业院校:',
            profession:'所学专业:',
            graduatedDate:'毕业时间:',
            graduatedCertifications:'毕业证书:'
          },
          careerInfo:{
            workDate:'从业时间:',
            gymnasiumName:'健身房全称(常驻):',
            gymnasiumAddress:'健身房地址(常驻):',
            workProve:'从业证明:',
            careerCertification:'资格证书:',
          }
        },
        infoloading:true,
        iconloading:false,
        certifate: false,
        defaultkey:'basicInfo',
        activekey: 'basicInfo',
        currentid:'',
        currentinfo:null,
        infostatus:null,
        certList: null,
        vertifyList: [],
        status: '',
        show: false,
        verify: false,
        number: 0,
        showVer: false,
        titleText: '教练管理',
        typeVerify: false,
        checkAll: false,
        isIndeterminate: true,
        visPhoto: false,
        checkAll1: false,
        checkAll2: false,
        isIndeterminate1: true,
        isIndeterminate2: true,
        type: ''
    }
  },
  components:{
    Tab,
    VertifyInput,
    VertifyPhoto
  },
  created () {
    this.list = getSessionStorage('detailInfo', {}) // 从之前的页面跳转传过来的数据
    this.status = this.$route.query.status
    this.currentid = this.$route.query.id
    this.type = this.$route.query.vertype
    this.startInit()
    this.getprivate() // 查询所有的私教课课程
    this.getgroupcourse() // 查询所有的团课课程
    this.queryspecial() // 查询所有特色课
    this.handleCheckAllChange()
    this.handleCheckAllChange2()
    this.handleCheckAllChange1()
    if (this.type === 'verify') {
      this.typeVerify = true
      this.titleText = '待审核教练'
      this.status = 'WaitingCertificated'
      this.list.specialCourseIds = []
      this.list.groupCourseIds = []
      this.list.privateCourseIds = []
      this.form = this.list
    } else {
      this.form = this.list.coach
      if (!this.form.specialCourseIds) {
        this.form.specialCourseIds = []
      }
      if (!this.form.groupCourseIds) {
        this.form.groupCourseIds = []
      }
      if (!this.form.privateCourseIds) {
        this.form.privateCourseIds = []
      }
    }
  },
  mounted () {
  },
  computed:{
    culculatestatus(){
      let results=[]
      let obj=this.currentinfo
      for(let i in obj){
        if(obj[i].hasOwnProperty('status')){
          results.push(obj[i]['status'])
        }else{
          for(let j in obj[i]){
            if(obj[i][j].length){
              obj[i][j].map((item)=>{
                results.push(item.status)
              })
            }
          }
        }
      }
      results=results.filter((item)=>{return !item})
      if(results.length) {
        this.show = results.length > 0
        return false
      }
      this.show = false
      return true
    }
  },
  methods: {
    // show () {
    //   this.isShow = !this.isShow
    // },
    hideSelect () {
      if (this.isShow) {
        this.isShow = false
      }
    },
    // 列表选择
    selcheck (item) {
      item.checked = !item.checked
    },
    // 删除选择
    deleteSel (item) {
      if (item.checked) {
        item.checked = false
      }
    },
    submit (ruleForm) {
      let self = this
      this.$refs[ruleForm].validate((valid) => {
        if (valid) {
          if (self.form.hireDate) {
            self.form.hireDate = new Date(self.form.hireDate).getTime()
          }
          let data = {
            branchId: self.form.branchId,
            hireDate: self.form.hireDate,
            courseLevelId: self.form.courseLevelId,
            coachBrief: self.form.coachBrief,
            groupCourseIds: self.form.groupCourseIds,
            specialCourseIds: self.form.specialCourseIds,
            privateCourseIds: self.form.privateCourseIds
          }
          console.log('教练介绍', self.form.coachBrief, self.form)
          updatecoach(self.form.personId, data).then(function (res) {
            if (isApiResponseOk(res)) {
              self.$message({
                showClose: true,
                message: '更新成功',
                type: 'success'
              })
              self.$router.go(-1)
            } else {
            }
          })
        }
      })
    },
    // 返回到教练管理页面
    goback () {
      if (this.typeVerify) {
        this.$router.push({name: 'waitvericoach'})
      } else {
        this.$router.push({name: 'coachlist'})
      }
    },
    // 获取所有的教练类别
    async getprivate () {
      const res = await getallprivate()
      if (isApiResponseOk(res)) {
        this.privateList = res.data
        if (!this.form.privateCourseIds) return
        if (this.form.privateCourseIds.length === this.privateList.length) {
          this.isIndeterminate2 = false
          this.checkAll2 = true
        } else {
          this.isIndeterminate2 = true
          this.checkAll2 = false
        }
        if (this.form.privateCourseIds.length === 0) {
          this.handleCheckAllChange2()
        }
      }
    },
    // 获取所有的团课
    async getgroupcourse () {
      const res = await getgroupsimple()
      if (isApiResponseOk(res)) {
        this.groupList = res.data
        if (this.form.groupCourseIds.length === this.groupList.length) {
          this.isIndeterminate = false
          this.checkAll = true
        } else {
          this.isIndeterminate = true
          this.checkAll = false
        }
        if (this.form.groupCourseIds.length === 0) {
          this.handleCheckAllChange()
        }
      }
    },
    getProv () {
    },
    startInit(){
      this.getCoachInfo()
    },
    async getCoachInfo(info=this.defaultkey){
        let data = {
          coachId: this.currentid,
          info: info
        }
        const res=await getCoachInfo(data)
        if (res.errorCode == 2000) {
          this.$message({
            message: res.errMsg,
            type: 'warning'
          })
          return
        }
        if(!res) return
        this.infostatus=res.data.status
        this.currentinfo=this.sort(res.data.info,info)
        this.visPhoto = true
        this.infoloading = false
      },
      changeTab(key,activekey){
        if(activekey==key) return;
        this.defaultkey=key;
        this.activekey=key
        this.currentinfo=null;
        this.verify = false;
        this.show = false;
        this.visPhoto = false
        this.getCoachInfo(key)
      },
      changeInputStatus(index){
        if(this.iconloading||this.iconloading) return;
        this.currentinfo[index]['status']=!this.currentinfo[index]['status']
        if (this.infostatus==='Completed' && !this.currentinfo[index]['status']) {
          this.showVer = true
        } else if (this.infostatus==='NotPassed' && this.currentinfo[index]['status']) {
          this.showVer = true
        } else if (this.infostatus==='HasChecked' && !this.currentinfo[index]['status'] ) {
          this.showVer = true
        }
      },
        // 对资质信息进行审核
      changeCertStatus(item){
        if(this.iconloading||this.iconloading) return;
        let obj = this.currentinfo[4]
        if (obj) {
          for (let val of Object.values(obj)){
            for (let data of val) {
              if (JSON.stringify(data).indexOf(item.name) > 0) {
                data.status = !item.status
                item.status = !item.status
                if (this.infostatus==='Completed' && !item.status) {
                  this.showVer = true
                } else if (this.infostatus==='NotPassed' && item.status) {
                  this.showVer = true
                } else if (this.infostatus==='HasChecked' && !item.status ) {
                  this.showVer = true
                }
                
              }
           }
          }
        }
      },
      changePhotoStatus(index1,index2,key2){
        if(this.iconloading||this.iconloading) return;
        this.currentinfo[index1][key2][index2]['status']=!this.currentinfo[index1][key2][index2]['status']
        if (this.infostatus==='Completed' && !this.currentinfo[index1][key2][index2]['status']) {
          this.showVer = true
        } else if (this.infostatus==='NotPassed' && this.currentinfo[index1][key2][index2]['status']) {
          this.showVer = true
        } else if (this.infostatus==='HasChecked' && !this.currentinfo[index1][key2][index2]['status'] ) {
          this.showVer = true
        }
        this.visPhoto = false
      },
      async vertify(){
        this.infoloading = true
        this.visPhoto = false
        const status=this.culculatestatus?'HasChecked':'NotPassed'
        this.currentinfo.map((item)=>{
            if (item.gymnasiumName){
              let status = item.status
              let list = item.gymnasiumName.split(' ')
              item.gymnasiumName = list[0]
              this.currentinfo.push({
                branchOffice: list[1],
                status: status
              })
            }
        })
        let data = {
            type:this.defaultkey,
            status:status,
            info:this.currentinfo
        }
        await validatecoachinfo({coachId:this.currentid}, data)
        let array = Object.keys(this.config.title)
        for(let i=0; i<array.length; i++) {
          if (this.activekey !== array[i]) {
            let data = {
              coachId: this.currentid,
              info: array[i]
            }
            const res=await getCoachInfo(data)

            if(((res.data.status === 'Completed') && (this.status === 'WaitingCertificated' || this.status === 'CertificateFailed')) || (res.data.status === 'NotPassed' && this.status === 'CertificateFailed')) {
              this.$nextTick(_ => {
                this.$refs.tab.click(array[i])
              })
              return
            }
          }
        }
        this.showVer = false
        this.verify = true
        this.vertifyList.push(this.activekey)
        this.number = this.number + 1
        this.infoloading = false
        // this.$message({
        //   showClose: true,
        //   message: '更新成功',
        //   type: 'success'
        // })
        await this.startInit()
      },
      /**
       * @description 遍历后台获取的数据，放到模板中
       */
      sort(currentinfo,key){
        let sort=[]
        let test = []
        this.certList = [];
        let gymnasiumName = ''
        if (currentinfo[4].careerCertificationNames) {
          this.certifate = true
        } else {
          this.certifate = false
        }
        for(let i in this.sortnames[key]){
          currentinfo.map((item)=>{
            let obj = item.careerCertificationNames
            if(obj) {
              for (let val of obj) {
                let arr = Object.values(val);
                if (test.indexOf(arr[0]) < 0) {
                  test.push(arr[0]);
                  this.certList.push({name: arr[0], status: arr[1]});
                }
              }
            }
            if (item.branchOffice){
              gymnasiumName = item.branchOffice
            }
            for(let j in item){
              if(this.sortnames[key][i]==j){
                sort.push(item)
              }
            }
          })
        }
        sort.map((item)=>{
            if (item.gymnasiumName){
              item.gymnasiumName = item.gymnasiumName  + ' ' + gymnasiumName
            }
        })
        return sort
      },
       handleCheckAllChange(val) {
        let list = []
        if (val) {
          for (var i = 0; i < this.groupList.length; i++) {
            list.push(this.groupList[i].id)
          }
          this.form.groupCourseIds = list
        } else {
          this.form.groupCourseIds = []
        }
        this.isIndeterminate = false
        console.log('this.indede', this.form.groupCourseIds, this.isIndeterminate)
      },
      handleCheckedCitiesChange(value) {
        let checkedCount = value.length
        this.checkAll = checkedCount === this.groupList.length
        this.isIndeterminate = checkedCount > 0 && checkedCount < this.groupList.length
      },
      handleCheckAllChange1(val) {
        let list = []
        if (val) {
          for (var i = 0; i < this.specialList.length; i++) {
            list.push(this.specialList[i].id)
          }
          this.form.specialCourseIds = list
        } else {
          this.form.specialCourseIds = []
        }
        this.isIndeterminate1 = false
      },
      handleCheckedCities(value) {
        let checkedCount = value.length
        this.checkAll1 = checkedCount === this.specialList.length
        this.isIndeterminate1 = checkedCount > 0 && checkedCount < this.specialList.length
      },
      handleCheckAllChange2(val) {
        let list = []
        if (val) {
          for (var i = 0; i < this.privateList.length; i++) {
            list.push(this.privateList[i].id)
          }
          this.form.privateCourseIds = list
        } else {
          this.form.privateCourseIds = []
        }
        this.isIndeterminate2 = false
      },
      handleCheckedPrivate (value) {
        let checkedCount = value.length
        this.checkAll2 = checkedCount === this.privateList.length
        this.isIndeterminate2 = checkedCount > 0 && checkedCount < this.privateList.length
      },
      async queryspecial () {
        const res = await queryspecial ()
        if (isApiResponseOk(res)) {
          for (var i = 0; i < res.data.length; i++) {
            res.data[i].id = parseInt(res.data[i].id)
            this.specialList.push(res.data[i])
          }
          if (!this.form.specialCourseIds) return
          if (this.form.specialCourseIds.length === this.specialList.length) {
            this.isIndeterminate1 = false
            this.checkAll1 = true
          } else if (this.form.specialCourseIds.length === 0) {
            this.handleCheckAllChange1()
          } else {
            this.isIndeterminate1 = true
            this.checkAll1 = false
          }
        }
      }
  },
  filters: {
      filterinput: function (value,key) {
        if(key=='sex'){
          value==0?value='男':value='女'
        }
        key=='birthday'?value=timeformer(value):''
        key=='graduatedDate'?value=timeformer(value):''
        if(key=='workDate'){
          value=`${value}年`
        }
        return value
      }
  }
}
</script>
<style lang="scss" scoped>
  $blue:#08D0EE;
  ul,li{
    padding: 0;
    margin: 0;
    list-style: none;
  }
  .coachInfo{
    display: flex;
    .verify{
      padding-bottom: .3rem;
      width: 39%;
      background: #fff;
      height: 100%;
    }
    .editInfo{
      background: #f3f7f9;
      flex: 1;
      margin-left: .15rem;
      margin-top: 0rem;
      .btn{
        padding-bottom: .2rem;
      }
      .el-form .el-date-editor{
        .el-input__inner{
          padding-right: .1rem;
        }
      }
      .el-checkbox-group{
        // margin-left: .25rem;
      }
    }
    .onlyVer{
      width: 100%;
      .tabbody .inputcontainer div.content{
        max-width: 5rem;
      }
    }
  }
  .tabbody{
    li.slot{
      .loadingcontainer{
        width:100%;
        height:100%;
        padding:0rem 0.6rem;
        background: white;
        display:flex;
        justify-content: space-between;
        flex-flow: column;
        .left{
          max-width: 4.6rem;
          flex-grow: 1;
        }
        .right{
          max-width: 6rem;
          justify-content: flex-end;
          display: flex;
          flex-wrap: wrap;
          margin-top: .24rem;
          .photos{
            max-width: 6rem;
          }
        }
      }
    }
  }
  .footer{
    margin-top: .3rem;
    display:flex;
    justify-content: center;
    .el-button--primary.is-plain.pass{
      color: #67C23A;
    }
    .el-button--primary.is-plain.unpass{
      color: #FC7563;
    }
    .el-button--primary.is-plain{
      font-size: .16rem;
      background: #FFFFFF;
      border: 1px solid #E9ECEF;
      border-radius: 4px;
      height: .44rem;
      &:disabled{
        border:1px solid #D8E0E4;
        color:#D8E0E4;
        background-color:transparent;
      }
      i{
        margin-right:.1rem;
      }
    }
    .el-button--primary+.el-button--primary{
      margin-left: .5rem;
    }
  }
  .certifate {
    display: flex;
    align-items: center;
    label {
      width: 1.8rem;
      padding: 0.30rem 0 0.30rem 0;
    }
    .right {
      width:100%;
      padding: 0.30rem 0 0.30rem 0;
      max-width: 2rem !important;
    }
  }
  .inputcontainer{
    &:first-child{
     padding-top: 0;
     }
    display: flex;
    width:100%;
    padding: 0.15rem 0 0.15rem 0;
    div.content{
      max-width:2rem;
      //border:1px solid red;
      margin-left: .3rem;
      margin-right: .3rem;
      //flex-grow: 1;
      display:flex;
      align-items: center;
    }
    div.tanhao{
      margin:0;
      padding:0;
      flex-grow: 1;
      //border:1px solid red;
      display:flex;
      align-items: center;
      justify-content: flex-end;
      i{
        color:#FC7563;
        margin:0;
        padding:0;
        font-size: .23rem;
      }
    }
  }
</style>



