<template>
  <div class="photos">
    <div>{{label}}</div>
    <ul class="list">
      <li v-for="(item,index) in photos" :key='index' @click.prevent="click(index)" @dblclick.prevent="dblclick(item,index)"><img :src="item.imgSrc" />
        <div class="hovershow" v-if="!item.status">
          <el-tooltip  class="item" effect="dark" content="点亮表达有误" placement="top">
            <i class="iconfont icon-tishi" ></i>
          </el-tooltip>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import * as urls from '../services/utils.js'
import {downMedia} from '../services/service.js'
import Vue from 'vue'
import { setTimeout } from 'timers';
  export default {
    name: 'photocomponent',
    props: ['photos', 'changephotostatus', 'label', 'visPhoto'],
    data () {
      return {
        topModelStatus: {
          classname: '',
          show: false,
          bodyhtml: '',
          closehtml: ''
        }
      }
    },
    mounted () {
      let self = this
      this.$nextTick(_=>{
        for (var i = 0; i < self.photos.length; i++) {
          let name='img'+i
          let index = i
          let id=self.photos[i][name]
          downMedia({mediaId: id}).then(function (res) {
            Vue.set(self.photos[index], 'imgSrc', res)
          })
        }
      })
    },
    watch: {
      'visPhoto': function () {
        let self = this
        this.$nextTick(_=>{
          for (var i = 0; i < self.photos.length; i++) {
            let name='img'+i
            let index = i
            let id=self.photos[i][name]
            downMedia({mediaId: id}).then(function (res) {
              Vue.set(self.photos[index], 'imgSrc', res)
            })
          }
        })
      }
    },
    methods:{
      click(index){
        this.changephotostatus(index)
      },
      dblclick(item,index){
        const src=item.imgSrc
        // this[urls.CHANGE_TOPMODELSTATUS_COMMIT]({
          
        // })
        this.createModal({
          show:true,
          bodyhtml:'<img src='+src+' />',
          classname:'photo',
          closehtml:'<div class="back"><i class="iconfont icon-icon_stop"></div></i>'
        })
        // this.topModelStatus = {
        //   show:true,
        //   bodyhtml:'<img src='+src+' />',
        //   classname:'photo',
        //   closehtml:'<div class="back"><i class="iconfont icon-guanbi"></div></i>'
        // }
      },
      // getimgAdress(item,index){
      // },
      clickme(){
        console.log('ahahha')
      },
      close(){
        this.topModelStatus = {
          show: false
        }
      }
    }
  }
</script>

<style lang="scss" scoped>

  .photos+.photos{
    margin-top: .5rem;
  }
  .photos{
    width:100%;
    display: flex;
  div{
    font-size: .14rem;
    white-space: nowrap;
    width: 1.3rem;
  }
  ul{
    margin: 0;
    padding: 0;
  }
  ul.list{
    // width:5.1rem !important;
  li{
    position:relative;
    margin:.1rem;
    margin-top: 0;
    margin-bottom: .2rem;
    width: 1.2rem;
    height: .72rem;
    background: #D8D8D8;
    list-style: none;
    display: inline-block;
    &:first-child{
      margin-left: 0;
    }
  .hovershow{
    right:0;
    bottom:0;
    width:100%;
    position:absolute;
  //background-color: rgba(0,0,0,.6);
    display:flex;
    justify-content: flex-end;
  i{
    margin-right: 3px;
    font-size: .2rem;
    color:#FC7563;
  }

  }

  img{
    width:100%;
    height:100%;
  }
  }
  }
  }
  .careerInfo{
    div{
      width: 1.8rem;
    }
  }
</style>



